/* Global styles */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

.contact-information{
  display: flex;
  gap: 10px;
  padding: 10px;
}



.links>a{

  margin: 10px;
}

.logo > img{
  width: 70px;
  background-color: white;
  border-radius: 50%;
}

.contact-container{
  display: flex;
  background-color: rgb(20, 2, 2);
  color: white;
  justify-content: space-between;
  align-items: center;
}

/* Header styles */
.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: #333;
  color: white;
}

.logo {
  flex-grow: 1;
  text-align: center;
}

.menu-button {
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 20px;
  cursor: pointer;
}

/* Dropdown menu styles */
.dropdown-content{
  padding: 10px;
  text-align: center;
  display: none;
  position: absolute;
  right: 0;
  justify-content: center;
  background-color: #333;
  min-width: 100%;
  color: #000;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  z-index: 1;
  transition: opacity 0.3s ease, transform 0.3s ease;
  transform: translateY(-10px);
}
.dropdown-content.open {
  display: block;
  transition: all;
  opacity: 1;
  transform: translateY(0);
}

.dropdown-content a {
  color: white;
  margin: auto;
  width: 50%;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {
  border-bottom:1px solid white ;
}



.timing{
  height: 100vh;
  display: flex;
}

.brown{
  background-color: rgb(20, 2, 2);
  color: white;
}

.timing-info{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 60%;
  margin: 10%;
}
/* Main section styles */
.main {
  height: 100vh;
  background-image: url('../public/images/main-background.jpg');
  background-size: cover;
  background-blend-mode: multiply;
  background-position: center;
  display: flex;
  flex-direction: column;
}

.background-tint {
  background-color: rgba(200,100,0,.5);
  background-blend-mode: multiply;
}


.main h1{
  border-right: 5px solid white;
}

.content p{
  opacity: 0.7;
  text-align: center;
  width: 100%;
}

.half-height {
  height: 50%;
  width: 50%;
  display: flex;
  align-items: center;
  color: white;
  text-align: center;
}

.content {
  font-size: 24px;
}

/* Footer styles */
.footer {
  background-color: rgb(20, 2, 2);
  color: white;
  padding: 20px;
  text-align: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.div-image1{
  background-image: url('../public/images/1.jpg');
  background-size: cover;
  background-position: center;

  width: 50%;
  margin: 10%;
  
}

.div-image2{
  background-image: url('../public/images/2.jpg');
  background-size: cover;
  background-position: center;

  width: 50%;
  margin: 10%;
}

.footer > div {
  width: 20%;
  margin-bottom: 20px;
  display: flex;
}
a,p{
  display: flex;
  gap: 10px;
  text-align: center;
  align-items: center;
}

.footer > div p,a{
  justify-content: center;
}
.contact-info, .hours, .follow, .address {
  display: flex;
  flex-direction: column;
}

.follow a {
  color: white;
  text-decoration: none;
  margin: 0 5px;
}

a {
  text-decoration: none;
  color: white;
}

.footer > div{
  border: 1px solid white;
  border-radius: 5px;
  color: black;
  background-color: rgb(216, 214, 214);
}

.footer>div>p>b{
  border-bottom: 1px solid black;
}

.follow > p > a{
  color: black;
}

/* Media queries for responsiveness */
@media (max-width: 768px) {
  /* Header styles */
  .header {
    flex-direction: column;
    align-items: center;
  }
  .contact-container{
    display: none;
  }

  .timing{
    margin-top: 20px;
  }
  .div-image1 {
    background-image: url('../public/images/3.png');
    background-size: cover;
    background-position: center;
    width: 60%; /* Adjust width to take full width on smaller screens */
    height: 100%; /* Set a fixed height for better visibility */
    margin: 20px; /* Adjust margin as needed */
  }

  .div-image2 {
    background-image: url('../public/images/2.jpg');
    background-size: cover;
    background-position: center;
    width: 60%; /* Adjust width to take full width on smaller screens */
    height: 100%; /* Set a fixed height for better visibility */
    margin: 20px; /* Adjust margin as needed */
  }

  .half-height{
    text-align: center;
    justify-content: center;
    align-self: center;
  }

  .timing{
    justify-content: center;
    align-items: center;
  }

  .timing-info{
    padding: 10px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
  }


  .menu-button {
    margin-top: 10px;
  }

  .timing{
    flex-direction: column;
  }

  .contact-container> div {
    flex-direction: column;
  }

  .links{
    display: flex;
    flex-direction: column;
  }
  /* Footer styles */
  .footer > div {
    width: 100%; /* Set the width to 100% for full width on smaller screens */
  }

  .footer {
    flex-direction: column; /* Stack footer items vertically on smaller screens */
  }
}
